import React from 'react';

export default class Boton extends React.Component {

    constructor(props){
        super(props);
        this.SelectorBoton = this.SelectorBoton.bind(this);
        this.disableButton = this.disableButton.bind(this);

    }

    SelectorBoton(bN) {
        switch(bN){
            case "Siguiente":
                return this.props.onChangeQuestion(bN);
            case "Anterior":
               return this.props.onChangeQuestion(bN);
            case "Submit":
               return this.props.onSubmit(this.props.questions);
            default:
              return ('') //cambiar para submit!!
        }
    }

    disableButton(i,bN,f){
        if ((i === 0 && bN === "Anterior") || (i === 9 && bN === "Siguiente") || (f===true)) return true;
        else return false;

    }

    render(){
        return(

            <button className="Botones"disabled={this.disableButton(this.props.indexQuestion, this.props.buttonName, this.props.finished)} onClick={()=>{this.SelectorBoton(this.props.buttonName)}}>{this.props.buttonName}</button>

        );
    }
}
