import React from 'react';
import Pregunta from './Pregunta.js';
import Respuesta from './Respuesta.js';

export default class Content extends React.Component {
  constructor(props){
		super(props);

        this.nuevasPreguntas = this.nuevasPreguntas.bind(this);
	}

    nuevasPreguntas() {

        fetch("https://quiz2019.herokuapp.com/api/quizzes/random10wa?token=752de40425a648bf8a49")
            .then(res => res.json())
            .then(json => {
                this.props.onInitQuestions(json)
            })
            .catch(error =>{
                console.log(error)
            });

    }



    render(){
        if (this.props.finished) return(

            <div className="ContentTerminado">
                <h1 className="Titulo">Practica 7 IWEB. Realizada por Gabriel y Julián</h1>
                <div className="Content">
                <img  className="Fotografia" src={this.props.question.attachment.url} alt="imagen"/>
                  <div className="Lateral">
                    <Pregunta  question={this.props.question}/>
                    <Respuesta question={this.props.question} onQuestionAnswer={this.props.onQuestionAnswer}/>
                    <h1 className="Titulo">TU SCORE ES: {this.props.score} </h1>
                    <button className="Botones"onClick={() => {this.nuevasPreguntas()}}>Reiniciar</button>
                  </div>
                </div>
            </div>
        );

        else return(
            <div>
              <h1 className="Titulo" >Practica 7 IWEB. Realizada por Gabriel y Julián</h1>
              <div className="Content">
                <img className="Fotografia" src={this.props.question.attachment.url} alt="imagen"/>
                <div className="Lateral">
                  <Pregunta question={this.props.question}/>
                  <Respuesta question={this.props.question} onQuestionAnswer={this.props.onQuestionAnswer}/>
                </div>
              </div>
            </div>
        );
    }
}
