import React from 'react';
import Content from './Content.js';
import ActionBar from './ActionBar.js';


export default class Game extends React.Component {

    render(){
        return(
            <div>
              <Content question={this.props.question} onQuestionAnswer={this.props.onQuestionAnswer} onInitQuestions={this.props.onInitQuestions} finished={this.props.finished} score={this.props.score}/>
              <ActionBar question={this.props.question} questions={this.props.questions} finished={this.props.finished} indexQuestion={this.props.indexQuestion} onChangeQuestion={this.props.onChangeQuestion} onSubmit={this.props.onSubmit}/>

            </div>

        );
    }
}
