import { combineReducers } from 'redux';
import { QUESTION_ANSWER, CHANGE_QUESION, SUBMIT, INIT_QUESTIONS } from './actions';


function score(state = 0, action = {}) {
    switch(action.type) {
        case SUBMIT:
            var score = state
            action.payload.map((question)=>{
              if ( question.userAnswer !== undefined){
                return JSON.stringify(question.answer).trim().toLowerCase()===JSON.stringify(question.userAnswer).trim().toLowerCase() ? score=score+1 : score}
              else {
                return score;
              }
                });
            return score;
        case INIT_QUESTIONS:
            return 0;
        default:
            return state;
    }
}

function finished(state = false, action = {}) {
    switch(action.type) {
        case SUBMIT:
            return true;
        case INIT_QUESTIONS:
            return false;
        default:
            return state;
    }
}
function currentQuestion(state = 0, action = {}) {
    switch(action.type) {
        case CHANGE_QUESION:
            if(action.payload==="Siguiente") return state + 1;
            else if(action.payload==="Anterior") return state - 1;
            break;
        case INIT_QUESTIONS:
            return 0;
        default:
            return state;
    }
}
function questions(state = [], action = {}) {
    switch(action.type) {
        case QUESTION_ANSWER:
            return state.map( (question,i) => {
                return { ...question, userAnswer: action.payload.index === i ? action.payload.answer : question.userAnswer}
            })
        case INIT_QUESTIONS:
            return action.payload;
        default:
            return state;
    }
}

const GlobalState = combineReducers({
    score,
    finished,
    currentQuestion,
    questions
});

export default GlobalState;
