import React, { Component } from 'react';
import './App.css';
import { connect } from 'react-redux';
import Game from './componentes/Game'
import {questionAnswer, changeQuestion, submit, initQuestions} from './redux/actions.js';


class App extends Component {

  render() {

      return (
      <div className = "Aplicacion">
        <Game question={this.props.questions[this.props.currentQuestion]}
              questions={this.props.questions}
              finished={this.props.finished}
              score={this.props.score}
              indexQuestion={this.props.currentQuestion}
              onQuestionAnswer={ (answer) => {this.props.dispatch(questionAnswer(this.props.currentQuestion, answer)) }}
              onChangeQuestion={ (buttonName) => {this.props.dispatch(changeQuestion(buttonName))}}
              onInitQuestions={ (questions) => {this.props.dispatch(initQuestions(questions))}}
              onSubmit={ (questions) => {this.props.dispatch(submit(questions))}}/>

      </div>
      );
    }
}


function mapStateToProps(state) {
  return {
    ...state
  };
}

export default connect(mapStateToProps)(App);
